<template>
  <div class="home">
    <swiper 
      class="swiper mainSwiper"
      ref='mainSwiper'
      :options='mainSwiper'
    >
      <!-- <swiper-slide><Slide0 /></swiper-slide> -->
      <swiper-slide><Slide2 /></swiper-slide>
      <!-- <swiper-slide><Slide24 :name='name'/></swiper-slide> -->
      <swiper-slide><Slide22 :name='name'/></swiper-slide>
      <!-- <swiper-slide><Slide22 /></swiper-slide> -->
      <swiper-slide><Slide23 /></swiper-slide>
      <swiper-slide><Slide24 /></swiper-slide>
      <swiper-slide><Slide21 /></swiper-slide>
      <!-- <swiper-slide><Slide11 :name='name' /></swiper-slide> -->
      <!-- <swiper-slide><Slide12 :name='name' /></swiper-slide> -->
      <!-- <swiper-slide><Slide11 :name='name' /></swiper-slide> -->
      <!-- <swiper-slide><Slide3 @inactive='inactive' @active='active' /></swiper-slide> -->
      <!-- <swiper-slide><Slide4 /></swiper-slide>
      <swiper-slide><Slide5 /></swiper-slide> -->
      <div class="swiper-pagination" slot='pagination'></div>
    </swiper>
  </div>
