<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

import Slide0 from '@/components/swipePage/Slide0.vue'
import Slide1 from '@/components/swipePage/Slide1.vue'
import Slide11 from '@/components/swipePage/Slide11.vue'
import Slide12 from '@/components/swipePage/Slide12.vue'
import Slide2 from '@/components/swipePage/Slide2.vue'
import Slide21 from '@/components/swipePage/Slide21.vue'
import Slide22 from '@/components/swipePage/Slide22.vue'
import Slide23 from '@/components/swipePage/Slide23.vue'
import Slide24 from '@/components/swipePage/Slide24.vue'
import Slide3 from '@/components/swipePage/Slide3.vue'
import Slide4 from '@/components/swipePage/Slide4.vue'
import Slide5 from '@/components/swipePage/Slide5.vue'

export default {
  name: 'Home',
  components: {
    Swiper, 
    SwiperSlide,
    Slide0,
    // Slide1,
    Slide11,
    Slide12,
    Slide2,
    Slide21,
    Slide22,
    Slide23,
    Slide24,
    Slide3,
    Slide4,
    Slide5,
  },
  mounted() {
    this.name = this.$route.query.name;
    console.log(this.name);
  },
  data() {
    return {
      name: '',
      mainSwiper: {
        direction: 'horizontal',
        slidePerView: 1,
        spaceBetween: 0,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        }
      }
    }
  },
  methods: {
    inactive: function() {
      console.log('allowClick :' + this.$refs.mainSwiper.$swiper.allowClick)
      console.log('allowSlideNext :' + this.$refs.mainSwiper.$swiper.allowSlideNext)
      console.log('allowSlidePrev :' + this.$refs.mainSwiper.$swiper.allowSlidePrev)
      console.log('allowTouchMove :' + this.$refs.mainSwiper.$swiper.allowTouchMove)
     
      this.$refs.mainSwiper.$swiper.allowClick = false
      this.$refs.mainSwiper.$swiper.allowSlideNext = false
      this.$refs.mainSwiper.$swiper.allowSlidePrev = false
      this.$refs.mainSwiper.$swiper.allowTouchMove = false

      console.log('allowClick :' + this.$refs.mainSwiper.$swiper.allowClick)
      console.log('allowSlideNext :' + this.$refs.mainSwiper.$swiper.allowSlideNext)
      console.log('allowSlidePrev :' + this.$refs.mainSwiper.$swiper.allowSlidePrev)
      console.log('allowTouchMove :' + this.$refs.mainSwiper.$swiper.allowTouchMove)
    },
    active: function() {
      console.log('allowClick :' + this.$refs.mainSwiper.$swiper.allowClick)
      console.log('allowSlideNext :' + this.$refs.mainSwiper.$swiper.allowSlideNext)
      console.log('allowSlidePrev :' + this.$refs.mainSwiper.$swiper.allowSlidePrev)
      console.log('allowTouchMove :' + this.$refs.mainSwiper.$swiper.allowTouchMove)
     
      this.$refs.mainSwiper.$swiper.allowClick = true
      this.$refs.mainSwiper.$swiper.allowSlideNext = true
      this.$refs.mainSwiper.$swiper.allowSlidePrev = true
      this.$refs.mainSwiper.$swiper.allowTouchMove = true

      console.log('allowClick :' + this.$refs.mainSwiper.$swiper.allowClick)
      console.log('allowSlideNext :' + this.$refs.mainSwiper.$swiper.allowSlideNext)
      console.log('allowSlidePrev :' + this.$refs.mainSwiper.$swiper.allowSlidePrev)
      console.log('allowTouchMove :' + this.$refs.mainSwiper.$swiper.allowTouchMove)
