<script>
export default {
  name: 'Slide2',
  props: [ 'name' ],
  methods: {
    kakao() {
      Kakao.Navi.start({
        name:'그 날의 온도',
        x:127.03391504953713,
        y:35.854843884541,
        coordType:'wgs84'
      })
      // window.location.replace('kakaomap://route?ep=35.854843884541,127.03391504953713&by=CAR');
    },
    naver() {
      let url = "nmap://route/car?dlat=35.854843884541&dlng=127.03391504953713&dname=%ea%b7%b8%20%eb%82%a0%ec%9d%98%20%ec%98%a8%eb%8f%84";
      let intentUrl = "intent://route/car?dlat=35.854843884541&dlng=127.03391504953713&dname=%ea%b7%b8%20%eb%82%a0%ec%9d%98%20%ec%98%a8%eb%8f%84#Intent;scheme=nmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.nhn.android.nmap;end"
      
      let mobile = this.getMobileInfo()
      if(mobile == "Android") {
        location.href = intentUrl
      } else if (mobile == "iOS") {
        this.openNaverMapApp(url)
      }
      // window.location.replace(url); 
    },
    tmap() {
      let url = "tmap://route?rGoName=%ea%b7%b8%20%eb%82%a0%ec%9d%98%20%ec%98%a8%eb%8f%84&rGoX=127.03391504953713&rGoY=35.854843884541"
      let intentUrl = "intent://route?rGoName=%ea%b7%b8%20%eb%82%a0%ec%9d%98%20%ec%98%a8%eb%8f%84&rGoX=127.03391504953713&rGoY=35.854843884541#Intent;scheme=tmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.skt.tmap.ku;end"
      
      let mobile = this.getMobileInfo()
      if(mobile == "Android") {
        location.href = intentUrl
      } else if (mobile == "iOS") {
        this.openTmapApp(url)
      }
    },
    getMobileInfo() {
      var userAgent = navigator.userAgent || navigator.vendor || window.opera;
      if (/windows phone/i.test(userAgent)) {
          return "Windows Phone";
      }
      if (/android|Android/.test(userAgent)) {
          return "Android";
      }
      if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
          return "iOS";
      }
      return "";
    },
    openNaverMapApp(url) {
      var clickedAt = +new Date();
      location.href = url;

      setTimeout(function() {
          if (+new Date() - clickedAt < 2000) {
              location.href = 'http://itunes.apple.com/app/id311867728?mt=8';
          }
      }, 1000);
    },
    openTmapApp(url) {
      var clickedAt = +new Date();
      location.href = url;

      setTimeout(function() {
          if (+new Date() - clickedAt < 2000) {
              location.href = 'https://itunes.apple.com/kr/app/t-map-for-all/id431589174?mt=8' 
          }
      }, 1000);
    }
  }
}
</script>

