<template>
  <div class='slide4Main'>
    <div class="slide4Backgroud"></div>
    <swiper
      class='swiper'
      :options='subSwiper'
    >
      <swiper-slide><Main1 /></swiper-slide>
      <swiper-slide><Main2 /></swiper-slide>
      <swiper-slide><Main3 /></swiper-slide>
      <swiper-slide><Main4 /></swiper-slide>
      <swiper-slide><MainETC /></swiper-slide>
      <div class="swiper-pagination" :id='test' slot='pagination' ref='subPagination'></div>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

import Main1 from '@/components/swipePage/swipeSubPage/Main1'
import Main2 from '@/components/swipePage/swipeSubPage/Main2'
import Main3 from '@/components/swipePage/swipeSubPage/Main3'
import Main4 from '@/components/swipePage/swipeSubPage/Main4'
import MainETC from '@/components/swipePage/swipeSubPage/MainETC'

export default {
  name: 'Slide4',
  components: {
    Swiper, 
    SwiperSlide,
    Main1,
    Main2,
    Main3,
    Main4,
    MainETC
  },
  mounted() {
    this.test = 'swiperTest'
  },
  data() {
    return {
      subSwiper: {
        slidePerView: 1,
        spaceBetween: 0,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      test: '',
    }
  }
}
</script>

<style>
.slide4Main {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.slide4Backgroud{
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../../assets/images/texture.png');
  position: absolute;
  opacity: .5;
}
.slide4Main .swiper-pagination-bullet-active {
  background-color: #3478F6;
}
#swiperTest {
  bottom: 10px;
}
</style>