<template>
  <div class='slide3Main'>
    <div class="slide3BackgroudImage"></div>
    <div class="slide3Backgroud">
      <div class="gridContainer">
        <img @click='showImage($event)' class='image' id='image1' src="../../assets/images/album/image1.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image2' src="../../assets/images/album/image2.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image3' src="../../assets/images/album/image3.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image4' src="../../assets/images/album/image4.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image5' src="../../assets/images/album/image5.png" alt="">
        <img @click='showImage($event)' class='image' id='image6' src="../../assets/images/album/image6.png" alt="">
        <img @click='showImage($event)' class='image' id='image7' src="../../assets/images/album/image7.png" alt="">
        <img @click='showImage($event)' class='image' id='image8' src="../../assets/images/album/image8.png" alt="">
        <img @click='showImage($event)' class='image' id='image9' src="../../assets/images/album/image9.png" alt="">
        <img @click='showImage($event)' class='image' id='image10' src="../../assets/images/album/image10.png" alt="">
        <img @click='showImage($event)' class='image' id='image11' src="../../assets/images/album/image11.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image12' src="../../assets/images/album/image12.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image13' src="../../assets/images/album/image13.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image14' src="../../assets/images/album/image14.jpg" alt="">
        <img @click='showImage($event)' class='image' id='image15' src="../../assets/images/album/image15.jpg" alt="">
      </div>
    </div>
    <div class="modal" v-show='isShow'>
      <div @click='closeModal' class="space"></div>
      <div class="imageArea">
        <swiper 
          class="swiper imageSwiper"
          ref='imageSwiper'
          :options='imageSwiper'
        >
          <swiper-slide><img src="../../assets/images/album/image1.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image2.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image3.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image4.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image5.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image6.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image7.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image8.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image9.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image10.png"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image11.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image12.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image13.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image14.jpg"></swiper-slide>
          <swiper-slide><img src="../../assets/images/album/image15.jpg"></swiper-slide>
          <div class="swiper-pagination" slot='pagination'></div>
        </swiper>
        <!-- <img src="" alt="">
        <img ref='imageArea' src="" alt="">
        <img src="" alt=""> -->
      </div>
      <div @click='closeModal' class="space"></div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'Slide3',
  components: {
    Swiper,
    SwiperSlide
  },
  mounted() {

  },
  data() {
    return {
      isShow: false,
      imageSwiper: {
        direction: 'horizontal',
        slidePerView: 1,
        spaceBetween: 0,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  methods: {
    showImage: function(e) {
      this.isShow = true
      var id = e.currentTarget.id.substring(5) - 1
      // this.$refs.imageArea.src = e.currentTarget.src
      this.$emit('inactive')
      this.$refs.imageSwiper.$swiper.activeIndex = id
    },
    closeModal: function() {
      this.isShow = false
      this.$emit('active')
    }
  }
}
</script>

<style>
.slide3Main {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.slide3BackgroudImage {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: black;
  /* background-image: url('../../assets/images/texture.png'); */
  /* opacity: .5; */
}
.slide3Backgroud{
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.gridContainer{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1px;
}
.modal {
  height: 100%;
  width: 100%;
  z-index:1;
  display: flex;
  flex-direction: column;
}
.space {
  flex-grow: 1;
  opacity: .6;
  background-color: black;
}
.imageArea {
  height: 280px;
  /* background-color: black; */
  background-color: rgba(0, 0, 0, 0.6);
}
.imageArea img {
  height: 280px;
  object-fit: contain;
}
.imageSwiper {
  height: 100%;
  width: 100%;
}
.slide3Main .swiper-pagination-bullet {
  width: 5px;
  height: 5px;
  background-color: #3478F6;
}
.slide3Main .swiper-pagination-bullet-active {
  background-color: #3478F6;
}
</style>